#mainDiv {
    width: 100%;
    height: 100%;
    padding-top: 40px;
}

#code {
    float: left;
    width: 100%;
    height: 400px;
    color: #333;
    font-family: "Consolas", "Monaco", "Bitstream Vera Sans Mono", "Courier New", "sans-serif";
    font-size: 12px
}

@media screen and (max-device-width: 768px){
    #code {
        font-size: 2em;
    }
}

#code .string {
    color: #2a36ff
}

#code .keyword {
    color: #7f0055;
    font-weight: bold
}

#code .placeholder {
    margin-left: 15px
}

#code .space {
    margin-left: 7px
}

#code .comments {
    color: #3f7f5f
}